import React, { useState, ReactNode } from "react";
import ArrowDownwardIcon from "@mui/icons-material/ArrowDownward";

interface ExpandableMenuProps {
  title: ReactNode; // 
  children: ReactNode; //   
  className?: string; //    
}

export const ExpandableMenu: React.FC<ExpandableMenuProps> = ({
  title,
  children,
  className = "",
}) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => setIsOpen(!isOpen);

  return (
    <div className={`max-w-[1200px] mx-auto pt-6 pb-4 ${className}`}>
      <button
        onClick={toggleMenu}
        className={`flex items-center text-left justify-between w-full px-2 text-lg transition-all 
          ${isOpen ? "text-white" : ""}
           hover:text-white`}
      >
        {/*  JSX  */}
        <span>{title}</span>

        {/*  */}
        <span
          className={`ml-2 flex min-w-8 items-center duration-300 justify-center transform transition-transform w-8 h-8 rounded-full bg-black text-white ${
            isOpen ? "rotate-180" : "bg-orange-500"
          }`}
        >
          <ArrowDownwardIcon />
        </span>
      </button>

      {/*  */}
      <div
        className={`mt-3 overflow-hidden transition-all ease-in-out duration-500 ${
          isOpen ? "max-h-screen" : "max-h-0"
        }`}
      >
        <ul className="box-rounded">{children}</ul>
      </div>
    </div>
  );
};

interface ExpandableImageMenuProps {
  title: ReactNode; // 
  image: ReactNode; // 
  children: ReactNode; //   
  className?: string; //    
}

export const ExpandableImageMenu: React.FC<ExpandableImageMenuProps> = ({
  title,
  children,
  image,
  className = "",
}) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => setIsOpen(!isOpen);

  return (
    <div className={`max-w-[1200px] mx-auto py-2 ${className}`}>
      <button
        onClick={toggleMenu}
        className={`flex text-left justify-between w-full px-2 py-2 text-lg transition-all 
          ${isOpen ? "text-white" : ""}
           hover:text-white`}
      >
        {/*  JSX  */}
        {title}

        {/*  */}
        <div className="flex flex-col justify-center h-[300px]">
          <span
            className={`ml-2 flex min-w-8 items-center duration-300 justify-center transform transition-transform w-8 h-8 rounded-full bg-black text-white ${
              isOpen ? "rotate-180" : "bg-orange-500"
            }`}
          >
            <ArrowDownwardIcon />
          </span>
        </div>

        {/*  JSX  */}
        <span>{image}</span>
      </button>
      {/*  */}
      <div
        className={`mt-3 overflow-hidden transition-all ease-in-out duration-500 ${
          isOpen ? "max-h-screen" : "max-h-0"
        }`}
      >
        <ul className="box-rounded">{children}</ul>
      </div>
    </div>
  );
};
