import React from "react";
import { useParams } from "react-router-dom";

const JobDetails: React.FC = () => {
  const { id } = useParams<{ id: string }>(); // URL jobId 

  //   
  const jobDetails = [
    {
      id: "1",
      title: " BI  ",
      part: "",
      career: " ",
      description: "       .",
    },
    {
      id: "2",
      title: "  ",
      part: "",
      career: "/",
      description: "        .",
    },
  ];

  // id    
  const job = jobDetails.find((job) => job.id === id);

  // id     
  if (!job) {
    return (
      <div className="p-8">
        <h1 className="text-2xl font-bold">    .</h1>
      </div>
    );
  }

  // id     
  return (
    <div className="p-8">
      <h1 className="text-2xl font-bold">{job.title}</h1>
      <p className="text-lg text-gray-600">{job.part}</p>
      <p className="text-lg text-gray-600">{job.career}</p>
      <p className="mt-4">{job.description}</p>
    </div>
  );
};

export default JobDetails;
