import React from "react";
import { useNavigate } from "react-router-dom"; // React Router 

interface TableRowProps {
  title: string;
  part: string;
  career: string;
  onClick?: () => void; //   
}

const Table: React.FC = () => {
  const navigate = useNavigate();

  const rows: TableRowProps[] = [
    {
      title: "     .",
      part: "",
      career: "",
      // onClick: () => navigate("/company/job-detail/1"), //     
    },
  ];

  return (
    <div>
      <div className="overflow-x-auto">
        <table className="min-w-full border-y border-neutral-300">
          <tbody className="">
            {rows.map((row, index) => (
              <tr
                key={index}
                className=" hover:bg-orange-50"
                onClick={row.onClick}
              >
                <td className="px-2 py-3 text-base md:text-lg md:py-4 text-neutral-800">
                  {row.title}
                </td>
                <td className="px-2 py-3 text-base md:text-lg md:py-4 text-neutral-800">
                  {row.part}
                </td>
                <td className="px-2 py-3 text-base md:text-lg md:py-4 text-neutral-800">
                  {row.career}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default Table;
