import React, { useEffect } from "react";
import { BrowserRouter as Router, Route, Routes, Link } from "react-router-dom";
import { LanguageProvider } from "./context/LanguageContext"; // LanguageProvider  
import ScrollToTop from "./components/ScrollToTop";
import Header from "./components/Header"; // Header   
import Footer from "./components/Footer"; // Footer   
import Home from "./pages/Home";
import About from "./pages/About";
import SingleCellRNA from "./pages/ServicesSingleCell";
import Bioinformatics from "./pages/Bioinformatics";
import NGS from "./pages/ServicesNGS";
import PrCenter from "./pages/PrCenter";
import Careers from "./pages/Careers";
import Contact from "./pages/Contact";
import Notice from "./pages/Notice";
import Privacy from "./pages/Privacy";
import JobDetails from "./pages/JobDetails";
import NoticeMarkdown from "./pages/NoticeMarkdown";
import Blog from "./pages/Blog";
import BlogMarkdown from "./pages/BlogMarkdown";
import "aos/dist/aos.css";
const AOS = require("aos");

const App: React.FC = () => {
  useEffect(() => {
    AOS.init({
      duration: 600, //   
      easing: "ease-in-out", //   
      once: true, //    
    });
  }, []);

  return (
    <LanguageProvider>
      <Router basename={process.env.PUBLIC_URL}>
        <ScrollToTop />
        <div className="flex flex-col min-h-screen bg-gray-50">
          <Header />
          {/* Main Content */}
          <main className="md:mt-24">
            <Routes>
              <Route path="/" element={<Home />} />
              <Route path="/company/aboutus" element={<About />} />
              <Route path="/company/careers" element={<Careers />} />
              <Route path="/company/job-detail/:id" element={<JobDetails />} />
              <Route path="/company/notice" element={<Notice />} />
              <Route path="/company/notice/:id" element={<NoticeMarkdown />} />
              <Route path="/services/scrna" element={<SingleCellRNA />} />
              <Route path="/bioinformatics" element={<Bioinformatics />} />
              <Route path="/services/ngs" element={<NGS />} />
              <Route path="/prcenter" element={<PrCenter />} />
              <Route path="/blog" element={<Blog />} />
              <Route path="/blog/:id" element={<BlogMarkdown />} />
              <Route path="/contact" element={<Contact />} />
              <Route path="/privacy" element={<Privacy />} />
            </Routes>
          </main>

          <Footer />
        </div>
      </Router>
    </LanguageProvider>
  );
};

export default App;
