import React from "react";
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import MarkdownRenderer from "../components/MarkdownRenderer";
import { Carousel } from "../components/Carousel";
import { ImageCard } from "../components/Card"; // ImageCard  

const BlogMarkdown: React.FC = () => {
  const { id } = useParams<{ id: string }>(); // URL :id  

  const filePath = `${process.env.PUBLIC_URL}/content/${id}.md`; // Markdown  

  const navigate = useNavigate();

  const handleNavigate = () => {
    navigate("/services/scrna");
  };

  const blogItems = [
    {
      title: "2024  ",
      image: "/genomics2024/images/[]243_.png",
      date: "2025.01.01.",
      onClick: () => navigate("/blog/newsletter"),
    },
  ];

  return (
    <div className="bg-white section-main">
      <div className="section-main-container">
        <div className="markdown-container min-h-[900px] flex justify-center">
          <div className="w-[900px]">
            <MarkdownRenderer filePath={filePath} />
            <div className="flex flex-col items-center justify-center my-24">
              <h2> scRNA-seq    .</h2>
              <div className="button-bold" onClick={handleNavigate}>
                 
              </div>
            </div>
            {/* <div className="w-full my-24">
              <h2>  </h2>
              <Carousel
                items={blogItems}
                visibleItemsCount={3}
                renderItem={(item) => (
                  <ImageCard
                    title={item.title}
                    image={item.image}
                    onClick={item.onClick}
                  >
                    <div className="text-base text-neutral-500">
                      {item.date}
                    </div>
                  </ImageCard>
                )}
              />
            </div> */}
          </div>
        </div>
      </div>
    </div>
  );
};

export default BlogMarkdown;
